<template>
  <div id="bankCard">
    <common-header title="选择银行卡">
      <slot>
        <span class="add" @click="linkTo('AddBankCard')"></span>
      </slot>
    </common-header>

    <div class="bankCard__list">
      <div class="card" v-for="(item,index) in cards" :key="index">
        <div class="card__top">
          <div class="top__name">{{item.accountBankName}}</div>
          <div class="top__num">{{item.accountIdCode}}</div>
        </div>
        <div class="card__bottom">
          <span class="del">删除</span>
        </div>
      </div>

      <!-- <div class="card">
        <div class="card__top">
          <div class="top__name">中国建设银行</div>
          <div class="top__num">**** **** **** 8888</div>
        </div>
        <div class="card__bottom">
          <span class="del">删除</span>
        </div>
      </div> -->
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        cards: []
      }
    },
    
    created() {
      this.getCardList();
    },

    methods: {
      getCardList() {
        this.$Api.getCardList().then((res) => {
          console.log(res);
          if(res.q.s == 0) {
            this.cards = res.q.cards;
          }
        })
      }
    }
  }
</script>


<style lang="scss">
  #bankCard {
    height: 100%;
    background: #f0f0f0;

    .add {
      position: absolute;
      right: 0.4rem;
      top: 50%;
      transform: translateY(-50%);
      width: 0.5867rem;
      height: 0.5867rem;
      background: url('../../../assets/add_btn.png');
      background-size: 100%;
    }

    .bankCard__list {
      .card {
        background: #fff;
        margin-bottom: 0.2667rem;

        .card__top {
          padding: 0.5rem 0.4rem;
          font-size: 0.48rem;
          border-bottom: 1px solid #eee;

          .top__name {
            padding-bottom: 0.2rem;
          }
        }

        .card__bottom {
          position: relative;
          height: 1.173rem;
          line-height: 1.173rem;

          .del {
            position: absolute;
            right: 0.4rem;
            color: #333;

            &::before {
              content: '';
              position: absolute;
              top: 50%;
              left: -0.7rem;
              margin-top: -0.24rem;
              width: 0.48rem;
              height: 0.48rem;
              background: url('../../../assets/common_btn_del@2x.png');
              background-size: 100%;
            }
          }
        }
      }
    }
  }
</style>



